* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

#about_section::after {
  content: "G";
  width: 100%;
  height: 100%;
  top: 0%;
  background: rgba(0, 0, 0, 0.4509803922);
  left: 0%;
  position: absolute;
  font-size: 46rem;
  color: rgba(189, 189, 189, 0.2588235294);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
}

#about_section {
  z-index: 2;
  position: relative;
  padding: 0 2rem;
  width: 100%;
  min-height: 80rem;
  padding: 5rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
#about_section .about_img_container {
  width: 7rem;
}

.about_left {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  flex-wrap: nowrap;
}
.about_left p {
  max-width: 44rem;
  text-align: right;
  margin: 2rem 0;
}
.about_left .Sub_heading h6 {
  color: #DCCA87;
  font-size: 3rem;
  margin: 2rem 0;
}

.about_right {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
}
.about_right p {
  max-width: 44rem;
  text-align: left;
  margin: 2rem 0;
}
.about_right .Sub_heading h6 {
  text-align: left;
  color: #DCCA87;
  font-size: 3rem;
  margin: 2rem 0;
}

#about_section .Sub_heading img {
  margin: 0 !important;
  width: 4rem !important;
}

@media only screen and (max-width: 1030px) {
  #about_section {
    flex-direction: column;
  }
  #about_section img {
    width: 4rem !important;
  }
  .about_left, .about_right {
    align-items: center !important;
  }
  .about_left p, .about_right p {
    text-align: center;
  }
  #about_section::after {
    font-size: 30rem;
  }
}/*# sourceMappingURL=about.css.map */