* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

html {
  font-size: 62.5%;
}

body {
  background: linear-gradient(#121212, #2a2a2a, #795548, #223944) !important;
}

.items .item_heading h1 {
  max-width: 17rem;
}

.todays_left {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.inner_todays_offer h2 {
  color: white;
  font-size: 3rem;
  align-self: center;
}

#todays_offer .heading_offer {
  font-size: 6rem;
  margin: 2rem 0 9rem 0 !important;
}

img, button {
  border-radius: 0% 32% 0% 32%;
}

#logo img {
  border-radius: 0% !important;
}

.animate_img {
  -webkit-animation-name: image_animation;
          animation-name: image_animation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes image_animation {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.04);
  }
}

@keyframes image_animation {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.04);
  }
}
#footer_logo {
  border-radius: 0px !important;
}

.same_bk {
  /* background: url("../assets/iii/back.svg") !important; */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

#navbar_links_container li a:hover::after {
  z-index: 3 !important;
  opacity: 1;
}

#navbar_links_container li a {
  position: relative;
  z-index: 1;
}
#navbar_links_container li a::after {
  transition: 1s;
  content: "";
  position: absolute;
  background-color: #ffffff;
  top: 130%;
  left: 0;
  width: 100%;
  height: 2%;
  opacity: 0;
  z-index: -3;
}

#navbar_links_container li a, #navbar_links_container li, #login_register_container a {
  color: white !important;
  font-size: 1.7rem;
  transition: 0.5s;
  cursor: pointer;
}
#navbar_links_container li a:hover, #navbar_links_container li:hover, #login_register_container a:hover {
  transform: scale(1.1) !important;
}

img {
  max-width: 100%;
}

p {
  color: #AAAAAA;
  font-family: Open Sans;
  font-size: 1.6rem;
  letter-spacing: 0.04em;
}

h1 {
  color: #DCCA87;
  font-family: Cormorant Upright;
}

button {
  background-color: #DCCA87;
  outline: none;
  border: none;
  transition: all 0.5s;
}
button:hover {
  color: rgb(15, 15, 15);
  transform: scale(1.1);
}

h6 {
  font-family: Open Sans;
}

.disable_scrolling {
  overflow: hidden !important;
}

body, html {
  overflow-x: hidden !important;
}/*# sourceMappingURL=index.css.map */