@import "../../../common_style_files/common";
body{
    background-color: black;
}
.layer{
    position: fixed;
    background-color: rgba(0, 0, 0, 0.904);
    height: 100vh;
    width: 100vw;
    // display: none;
    top: 0;
    left: 0;

}
#main_navbar
{
    padding: 2.5rem 0rem;
    position:  relative !important;
    width: 100%;
   @include flex(center,center,nowrap,row);
}
#logo{
    width: 15rem;
    img{
        max-width: 100%;
    }
}
.logo_bars{
    display: none;
}
#inner_nav_parent{
    width: 94%;
    max-width: 110rem;

   @include flex(space-between,center,nowrap,row);

}
#navbar_links_container{
    //   position: absolute;
    //   top: 50%;
    //   left : 50%;
    //   transform: translate(-50%,-50%);
        li{
            margin: 0rem 1.5rem;
         a{
            font-family: Open Sans;
            font-style: normal;
            font-weight: normal;
         }
        }
    @include flex(center,center,nowrap,row);
}

#login_register_container{
    @include flex(center,center,nowrap,row);
    
    #login_register{
        @include flex(center,center,nowrap,row);

    }
    span{
        color: white;
        margin: 0 1.5rem;
        font-size: 2rem;
    }
}

.nav_toggle_link{
    display: none;
}
// media queries starting 
@media only screen and (max-width:900px)
{

    .logo_bars{
        display: inline-block !important;
        color: white;
        font-size: 2rem;
    }
      
 #navbar_links_container
{
    transition: 0.8s;

    position: absolute;
    top: 100%;
    left: 100%;
    width: 100%;
    background: linear-gradient( black, #161616b5);
    @include flex(center,center,nowrap,column);
    li{
        margin: 1rem 0;
    }
    .nav_toggle_link{
        display: block;
    }
}
#login_register_container{
    
   
        display: none;
   
}
#logo_container{
    width: 100%;
    @include flex(space-between,center,nowrap,row);
}
}

@media only screen and (max-width:700px)
{
   #inner_nav_parent{
       width: 97%;
   }
   #logo{
    width: 11rem;
   }
   #main_navbar {
    padding: 1.5rem 0rem;
   }

}