@import "../../common_style_files/common";

#gallery{
padding: 5rem 0.5rem 2rem 0.5rem;
     min-height: 30rem;
     .Sub_heading{
          margin-right: 15rem !important;
     }
     p{
          text-align: center;
          font-size: 1.6rem;
          max-width: 36rem;
          margin: 1rem 0;
          word-break: break-all;
     }
     @include flex(center,center,nowrap,column);
     
     
}
.gallery_heading
{
     font-size: 4rem;
     margin-bottom: 2rem;

}
.insta_icon{
     position: absolute;
     top: 50%;
     left: 50%;
     transform: translate(-50%,-50%);
     width: 3rem;
     z-index: -5;
     transition: 0.5s;
     &:hover{
         width: 3.5rem;
         cursor: pointer;
     }

}
.gallery_images_container{
     margin: 4rem 0; 
     
     @include flex(center,center,wrap,row);
     width: 100%;
     div{
          position: relative;
          z-index: 1;
          &::after{
                 z-index: -2 !important;
                 content: "";
                 position: absolute;
                 top: 0;
                 left: 0;
                 width: 100%;
                 border-radius: 0% 32% 0% 32%;
                 height: 100%;
                 z-index: 2;
                 background-color: rgba(0, 0, 0, 0.603);
            }
          // img{
               width: 20rem;
               transition: 0.5s;
               margin: 1rem;
          // }
     }
}
.gallery_images_container div:hover  {
     transform: scale(1.1);

}
.gallery_images_container div:hover .insta_icon{

     z-index: 5 !important;
}
.gallery_images_container div:hover::after{
     z-index: 3 !important;

}