@import"./_common";
html{
    font-size: 62.5%;
}
body{
    background: linear-gradient( #121212,#2a2a2a, #795548, #223944) !important;
}
.items .item_heading h1 {
    max-width: 17rem;
}
.todays_left{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
}
    .inner_todays_offer h2 {
        color: white;
        font-size: 3rem;
        align-self: center;
    }
#todays_offer .heading_offer {
    font-size: 6rem;
    margin: 2rem 0 9rem 0 !important;
}
@mixin c_back()
{
    
    background: url("../assets/iii/back.svg") !important;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    @include flex(space-evenly,center,wrap,row);
    width: 100%;
}
// section{padding: 1rem !important;}
img,button{
    border-radius: 0% 32% 0% 32%;
}
#logo{
    img{
        border-radius: 0% !important;
    }
}
.animate_img{
    animation-name: image_animation ;
    animation-duration: 1s;
    animation-delay: 0s;
    animation-direction: alternate;
    animation-iteration-count: infinite;
}
@keyframes image_animation 
{
    0%{

        transform: scale(1);
    }
    100%{

        transform: scale(1.04);

    }
    
}



#footer_logo {
    border-radius:0px !important; 

}
.same_bk{
    @include c_back();
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}
#navbar_links_container li a:hover::after
{
    z-index: 3 !important;
    opacity: 1;

} 
#navbar_links_container li a {
    position: relative;
    z-index: 1;

    &::after{
        transition: 1s;
        content: "";
        position: absolute;
        background-color: #ffffff;
        top: 130%;
        left: 0;
        width: 100%;
        height: 2%;
        opacity: 0;
        z-index: -3;
    }
}
#navbar_links_container li a,#navbar_links_container li ,#login_register_container a{
    color: white !important;
    font-size: 1.7rem;

    transition: 0.5s;
    cursor: pointer;
    &:hover{
        transform: scale(1.1) !important;
    }
}
img{
    max-width: 100%;
}
p{
    color: $secondary_color;
    font-family: $f2;
    font-size: 1.6rem;
    letter-spacing: 0.04em;
}
h1{
    color: $primary_color;
    font-family: $f1;
    
}
button{
    background-color: $primary_color;
    outline: none;
    border: none;
    transition: all 0.5s;
    &:hover{
        color: rgb(15, 15, 15);

        transform: scale(1.1);
    }
}
h6{
    font-family: $f2;

}
.disable_scrolling{
    overflow: hidden !important;
}
body,html{
    overflow-x: hidden !important;
}

