@import "../../../common_style_files/common";

.Sub_heading{
    color: $secondary_color;
    font-size: 1.2rem;
    h6{
        width: 100%;
        text-align: center;
        letter-spacing: 0.04em;
        font-size: 1.3rem;
    }
    img{
        width: 3rem;
    }
}
.sub_left{
    @include flex(center,flex-start,nowrap,column);
    img{
        margin: 1rem 0;
    }
}
.sub_right{
    @include flex(center,flex-end,nowrap,column);
 img{
     margin: 1rem 0;
 }
}
.sub_center{
    @include flex(center,center,nowrap,column);
    img{
        margin: 1rem 0;
    }
}

// button component 
.common_btn
{
    margin: 2rem 0;
    padding: 1rem 2rem;
    color: rgb(15, 15, 15);
      
}
//____________________________
// list items css 

.items{
    margin: 2rem 0;
    @include flex(center,center,nowrap,column);
    .item_heading
    {
        margin-bottom: 1rem;
        h1{
            font-size: 2.4rem !important;
            margin: 0rem 2rem 0rem 0rem !important;
        }
        @include flex(space-between,center,nowrap,row);
        width: 100%;
        div{
            img{
            margin-right: 2rem;
            width: 11rem;
            }
            @include flex(center,center,nowrap,row);

            h3{
                color: white;
                font-size: 1.8rem !important;

            }
        }

    }
    h6{
        color: white;
        font-size: 1.3rem !important;

        width: 100%;
        text-align: left;
    }
}
//____________________________

// award
.award{
 margin: 1.5rem 0;
    @include flex(center,flex-end,nowrap,row);
    img{
        width: 5rem;
        margin-right: 2rem;
    }
    .awar_text{
        @include flex(center,start,nowrap,column);
        h1{
                   text-align: left;
                   font-size: 2rem;
                   margin-bottom: 1rem;
                }
                p{
                    text-align: left;
                    color: $secondary_color;
                    
    max-width: 18rem;

                    font-size: 1.5rem;
               }
    }
}