* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

#footer {
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  padding: 0 1rem;
  padding-bottom: 4rem;
}

.new_letter {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  max-width: 100rem;
  width: 80%;
  background: rgba(12, 12, 12, 0.4784313725);
  padding: 4rem 1rem;
}
.new_letter div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin: 1.2rem 0;
}
.new_letter h1 {
  font-size: 3.5rem;
  text-align: center;
  margin: 0rem 0 1rem 0;
}
.new_letter P {
  text-align: center;
  margin: 1rem 0;
  font-size: 1.5rem;
}
.new_letter input {
  background-color: transparent;
  padding: 1rem;
  margin: 0 1rem;
  max-width: 39rem;
  width: 90%;
  border: 1px solid white;
  outline: none;
  color: white;
}
.new_letter input::-moz-placeholder {
  color: rgb(179, 179, 179);
}
.new_letter input:-ms-input-placeholder {
  color: rgb(179, 179, 179);
}
.new_letter input::placeholder {
  color: rgb(179, 179, 179);
}

.footer_links {
  padding: 8rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: start;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.footer_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
.footer_icon i {
  margin: 0 0.5rem;
  color: white;
  font-size: 2rem;
  transition: 0.5s;
}
.footer_icon i:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.footer_brand, .footer_contact, .footer_timing {
  margin: 2rem;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
.footer_brand p, .footer_contact p, .footer_timing p {
  color: white;
  margin: 0.5rem 0;
  max-width: 41rem;
  text-align: center;
}
.footer_brand h1, .footer_contact h1, .footer_timing h1 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
}

#footer_logo {
  margin-bottom: 2rem;
  width: 18rem;
}

#footer_symbol {
  margin: 1rem 0;
  width: 3rem;
}

.copy_right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  text-align: center;
  font-size: 1.4rem;
  padding-bottom: 6rem;
  color: white;
  width: 100%;
}/*# sourceMappingURL=footer.css.map */