@import "../../common_style_files/common";
#footer{
    @include flex(start,center,nowrap,column);
    width: 100%;
    padding: 0 1rem;
    padding-bottom: 4rem ;
}
.new_letter{
    @include flex(center,center,nowrap,column);
    max-width: 100rem;
    width: 80%;
   background: #0c0c0c7a;
   
   padding: 4rem 1rem;
   div{
       @include flex(center,center,wrap,row);
       width: 100%;

       margin: 1.2rem 0;
    }
    h1{
        font-size: 3.5rem;
        text-align: center;
        margin: 0rem  0 1rem 0;
    }
    P{
        text-align: center;
          margin: 1rem 0;
          font-size: 1.5rem;
      }
    input{
        background-color: transparent;
        padding: 1rem;
        margin: 0 1rem;

            max-width: 39rem;
            width: 90%;
            border: 1px solid white;
            outline: none;
            color: white;
            &::placeholder{
                color: rgb(179, 179, 179);

            }
    }

}
.footer_links{
    padding: 8rem 0;
    @include flex(space-evenly,start,wrap,row);
    width: 100%
}
.footer_icon{
    
    @include flex(center,center,nowrap,row);
    i{
        margin: 0 0.5rem;
        color: white;
        font-size: 2rem;
        transition: 0.5s;
        &:hover{
            cursor: pointer;
            transform: scale(1.1);
        }
    }
}
.footer_brand,.footer_contact,.footer_timing
{

    margin: 2rem;
    @include flex(start,center,nowrap,column);
    p{
        color: white;
        margin: 0.5rem 0;
        max-width: 41rem;
        text-align: center;
    }
    h1{
        font-size: 2.5rem;
        margin-bottom: 2rem;
    }
}
#footer_logo{
    margin-bottom: 2rem;
    
    width: 18rem;
}
#footer_symbol{
    margin: 1rem 0;

    width: 3rem;
}
.copy_right{
    @include flex(center,center,nowrap,column);
text-align: center;
font-size: 1.4rem;
padding-bottom: 6rem;
color: white;
width: 100%;
}