* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

#find_us {
  width: 100%;
  color: red;
  padding: 1rem;
  margin: 5rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.find_section_heading_container {
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  flex-wrap: nowrap;
}
.find_section_heading_container h6 {
  font-size: 1.4rem;
  color: white;
  margin: 1rem 0;
}
.find_section_heading_container P {
  font-size: 1.3rem;
  color: white;
  margin: 2rem 0;
}
.find_section_heading_container h2 {
  color: #DCCA87;
  font-size: 2rem;
}
.find_section_heading_container h1 {
  margin: 0.5rem 0 4rem 0;
  font-size: 3rem;
}

.find_us_image_container {
  margin: 1rem 2rem;
  width: 33rem;
}/*# sourceMappingURL=find_us.css.map */