* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

body {
  background-color: black;
}

.layer {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.904);
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
}

#main_navbar {
  padding: 2.5rem 0rem;
  position: relative !important;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

#logo {
  width: 15rem;
}
#logo img {
  max-width: 100%;
}

.logo_bars {
  display: none;
}

#inner_nav_parent {
  width: 94%;
  max-width: 110rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

#navbar_links_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
#navbar_links_container li {
  margin: 0rem 1.5rem;
}
#navbar_links_container li a {
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
}

#login_register_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
#login_register_container #login_register {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
#login_register_container span {
  color: white;
  margin: 0 1.5rem;
  font-size: 2rem;
}

.nav_toggle_link {
  display: none;
}

@media only screen and (max-width: 900px) {
  .logo_bars {
    display: inline-block !important;
    color: white;
    font-size: 2rem;
  }
  #navbar_links_container {
    transition: 0.8s;
    position: absolute;
    top: 100%;
    left: 100%;
    width: 100%;
    background: linear-gradient(black, rgba(22, 22, 22, 0.7098039216));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  #navbar_links_container li {
    margin: 1rem 0;
  }
  #navbar_links_container .nav_toggle_link {
    display: block;
  }
  #login_register_container {
    display: none;
  }
  #logo_container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
  }
}
@media only screen and (max-width: 700px) {
  #inner_nav_parent {
    width: 97%;
  }
  #logo {
    width: 11rem;
  }
  #main_navbar {
    padding: 1.5rem 0rem;
  }
}/*# sourceMappingURL=nav.css.map */