@import "../../common_style_files/common";
#we_believe{
    padding: 5rem 1rem;
    min-height: 60rem;
    width: 100%;
    // background: url("../../assets/");
    // background-size: cover;
    // background-repeat: no-repeat;
    // background-position: center;
    // background-attachment: fixed;
    @include flex(space-evenly,start,wrap,row);
    
    
}

//img 
.we_believe_img_container{
    width: 36rem;
    
    margin: 0 2rem;
}
// heading 
.we_believe_heading_container{
    padding-top: 4rem;
    @include flex(center,start,nowrap,column);
    p,#admin_info{
        margin: 2rem 0;
    }
    #sign{
        margin: 3rem 0;
    }
    #head{
        font-size: 5rem;
        margin: 1rem 0 2rem 0;
    }
    #admin_info{
        h1{
            font-size: 2.7rem;
            margin: 1rem 0;
        }
        h4{
            font-size: 1.8rem;
            color: white;

        }
    }
    p{
        max-width: 42rem;

    }
}