@import "../../common_style_files/common";
#todays_offer
{
    width: 100%;
    padding: 3rem 1rem;
    @include flexcc_col;
    h1{
        font-size: 6rem;
        margin: 2rem 0 4rem 0;
    }
}

.inner_todays_offer{
    margin-bottom: 3rem ;
    @include flex(center,start,nowrap,row);
    h2{
        color: white;
        font-size: 3rem;
    }
    .todays_img_container{

        width: 33rem;
        margin: 0px 4rem;
    }
   


}

.todays_left,.todays_right{
    @include flexcc_col;
    h1{
        margin-bottom: 3rem;
    }
}

@media only screen and (max-width:1040px) 
{
    
    .inner_todays_offer{
        @include flex(center,center,nowrap,column);
        .todays_img_container{
          margin: 2rem 0;
        width: 23rem;

      }
    
}
}