    width: 100%;
    margin:4rem 0;
     @include flex(space-around,center,nowrap,row);
     
     h1{
        max-width: 35rem;
        font-size: 6rem;
        line-height: 8rem;
        letter-spacing: 0.04em;
     }
   P{
    font-size: 1.6rem;
    max-width: 34rem;
