* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

#todays_offer {
  width: 100%;
  padding: 3rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#todays_offer h1 {
  font-size: 6rem;
  margin: 2rem 0 4rem 0;
}

.inner_todays_offer {
  margin-bottom: 3rem;
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: row;
  flex-wrap: nowrap;
}
.inner_todays_offer h2 {
  color: white;
  font-size: 3rem;
}
.inner_todays_offer .todays_img_container {
  width: 33rem;
  margin: 0px 4rem;
}

.todays_left, .todays_right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.todays_left h1, .todays_right h1 {
  margin-bottom: 3rem;
}

@media only screen and (max-width: 1040px) {
  .inner_todays_offer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .inner_todays_offer .todays_img_container {
    margin: 2rem 0;
    width: 23rem;
  }
}/*# sourceMappingURL=todays_offer.css.map */