*{
    text-decoration: none !important;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    list-style: none;
}
html,body{
    min-height: 100vh;
    width: 100%;
}



input,textarea,button{
    transition: all 0.5s;
}
button{
    text-transform: capitalize;
    cursor: pointer;
}

// mixin starting 

// flex box 
@mixin flex($js,$al,$wr,$dr) 
{
    display: flex;
    justify-content: $js;
    align-items: $al;
    flex-direction: $dr;
    flex-wrap: $wr;
    
}




@mixin flexcc 
{
    display: flex;
    justify-content: center;
    align-items: center;
    
}
@mixin flexcc_wrap 
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
//............................................
@mixin flexcc_col 
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}
@mixin flexcc_col_wrap 
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    
}
//custom mixin



// flex box 
// Buttons & other
.dnone{
    display: none !important;
}
.znone{
    z-index: -100;
}
.zhigh{
    z-index: 100;
}
.zmid{
    z-index: 50;
}
@mixin shadow{
    box-shadow: 4px -5px 25px #00000094;
}

@mixin btn
{
    width: auto;
    height: auto;
    padding: 1rem  1.5rem;
    outline: none;
    border: none;
    font-size: 2rem;
    
} 
@mixin btn_c($yp,$xp,$color,$font)
{
    width: auto;
    height: auto;
    padding: $yp  $xp;
    outline: none;
    border: none;
    color: $color;
    font-size: $font;
    
} 
.hover_effect{
transition: all 0.7s;
&:hover
{
transform: scale(1.1);
    cursor: pointer;
}

}
@mixin after_tl($t,$l,$w,$h){
    content:"";
    position:  absolute;
    top:$t;
    left:$l;
    width: $w;
    height:$h;
    transition: 0.6s;
}
//common variables

$primary_color:#DCCA87;
$secondary_color:#AAAAAA;
$f1: Cormorant Upright;
$f2: Open Sans;






//common ---------------------




input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}
