* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

#we_believe {
  padding: 5rem 1rem;
  min-height: 60rem;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: start;
  flex-direction: row;
  flex-wrap: wrap;
}

.we_believe_img_container {
  width: 36rem;
  margin: 0 2rem;
}

.we_believe_heading_container {
  padding-top: 4rem;
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  flex-wrap: nowrap;
}
.we_believe_heading_container p, .we_believe_heading_container #admin_info {
  margin: 2rem 0;
}
.we_believe_heading_container #sign {
  margin: 3rem 0;
}
.we_believe_heading_container #head {
  font-size: 5rem;
  margin: 1rem 0 2rem 0;
}
.we_believe_heading_container #admin_info h1 {
  font-size: 2.7rem;
  margin: 1rem 0;
}
.we_believe_heading_container #admin_info h4 {
  font-size: 1.8rem;
  color: white;
}
.we_believe_heading_container p {
  max-width: 42rem;
}/*# sourceMappingURL=we_believe.css.map */