@import "../../common_style_files//common";
#video_section{
min-height: 52rem;
width: 100%;
background: linear-gradient(rgba(0, 0, 0, 0.616),rgba(0, 0, 0, 0.695)), url("../../assets/iii/video.jpg") !important;
background-size: cover;
background-repeat: no-repeat;
background-position: center;
@include flexcc();
img{
    font-size: 2.5rem;
    transition: 0.5s;
    &:hover{
        transform: scale(1.1);
    }
}
}