* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

#gallery {
  padding: 5rem 0.5rem 2rem 0.5rem;
  min-height: 30rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
#gallery .Sub_heading {
  margin-right: 15rem !important;
}
#gallery p {
  text-align: center;
  font-size: 1.6rem;
  max-width: 36rem;
  margin: 1rem 0;
  word-break: break-all;
}

.gallery_heading {
  font-size: 4rem;
  margin-bottom: 2rem;
}

.insta_icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 3rem;
  z-index: -5;
  transition: 0.5s;
}
.insta_icon:hover {
  width: 3.5rem;
  cursor: pointer;
}

.gallery_images_container {
  margin: 4rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}
.gallery_images_container div {
  position: relative;
  z-index: 1;
  width: 20rem;
  transition: 0.5s;
  margin: 1rem;
}
.gallery_images_container div::after {
  z-index: -2 !important;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 0% 32% 0% 32%;
  height: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.603);
}

.gallery_images_container div:hover {
  transform: scale(1.1);
}

.gallery_images_container div:hover .insta_icon {
  z-index: 5 !important;
}

.gallery_images_container div:hover::after {
  z-index: 3 !important;
}/*# sourceMappingURL=gallery.css.map */