* {
  text-decoration: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  min-height: 100vh;
  width: 100%;
}

input, textarea, button {
  transition: all 0.5s;
}

button {
  text-transform: capitalize;
  cursor: pointer;
}

.dnone {
  display: none !important;
}

.znone {
  z-index: -100;
}

.zhigh {
  z-index: 100;
}

.zmid {
  z-index: 50;
}

.hover_effect {
  transition: all 0.7s;
}
.hover_effect:hover {
  transform: scale(1.1);
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none !important;
  -webkit-text-fill-color: white;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

.Sub_heading {
  color: #AAAAAA;
  font-size: 1.2rem;
}
.Sub_heading h6 {
  width: 100%;
  text-align: center;
  letter-spacing: 0.04em;
  font-size: 1.3rem;
}
.Sub_heading img {
  width: 3rem;
}

.sub_left {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
}
.sub_left img {
  margin: 1rem 0;
}

.sub_right {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  flex-wrap: nowrap;
}
.sub_right img {
  margin: 1rem 0;
}

.sub_center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
.sub_center img {
  margin: 1rem 0;
}

.common_btn {
  margin: 2rem 0;
  padding: 1rem 2rem;
  color: rgb(15, 15, 15);
}

.items {
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
.items .item_heading {
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
}
.items .item_heading h1 {
  font-size: 2.4rem !important;
  margin: 0rem 2rem 0rem 0rem !important;
}
.items .item_heading div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
.items .item_heading div img {
  margin-right: 2rem;
  width: 11rem;
}
.items .item_heading div h3 {
  color: white;
  font-size: 1.8rem !important;
}
.items h6 {
  color: white;
  font-size: 1.3rem !important;
  width: 100%;
  text-align: left;
}

.award {
  margin: 1.5rem 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: row;
  flex-wrap: nowrap;
}
.award img {
  width: 5rem;
  margin-right: 2rem;
}
.award .awar_text {
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  flex-wrap: nowrap;
}
.award .awar_text h1 {
  text-align: left;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.award .awar_text p {
  text-align: left;
  color: #AAAAAA;
  max-width: 18rem;
  font-size: 1.5rem;
}/*# sourceMappingURL=all.css.map */