@import "../../common_style_files/common";

#about_section::after{
    content: "G";
    width: 100%;
    height: 100%;
    top: 0%;
    background: #00000073;
    left: 0%;
    position: absolute;
    font-size: 46rem;
    color: #bdbdbd42;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -1;
}

#about_section{
    z-index: 2;
    position: relative;
    padding: 0 2rem;
    width: 100%;
    min-height: 80rem;
    // background: url("../../assets/iii/back.jpg") !important;
    // background: fixed;
    // background-position: center;
    // background-size: cover;
    // background-repeat: no-repeat;
    // background-attachment: fixed;
padding: 5rem 0;
    @include flex(space-evenly,center,nowrap,row);
    .about_img_container{

        width: 7rem;
    }
  
    
}
.about_left{
    @include flex(center,flex-end,nowrap,column);
    p{

        max-width: 44rem;
        text-align: right;
        margin: 2rem 0;

    }
    .Sub_heading h6 {
        color: $primary_color;
        font-size: 3rem;
        margin: 2rem 0;

    }

}
.about_right{
    @include flex(center,flex-start,nowrap,column);
    p{

        max-width: 44rem;
        text-align: left;
        margin: 2rem 0;
    }
    .Sub_heading h6 {
        text-align: left;
        color: $primary_color;
        font-size: 3rem;
        margin: 2rem 0;

    }


}
#about_section .Sub_heading img
{
    margin: 0 !important;
    width: 4rem !important;
}
@media only screen and (max-width:1030px) 
{
    #about_section{
        flex-direction: column;
        img{
            width: 4rem !important;
        }
    }
    .about_left,.about_right{
        align-items: center !important;
        p{
            text-align: center;
        }
    }
#about_section::after{
    font-size: 30rem;
}
}